import {
    FunctionComponent,
    useState,
    useCallback,
    useEffect,
    useRef,
    useMemo,
} from "react";
import {
    Button,
    CircularProgress,
    Icon,
    Toolbar,
    Breadcrumbs,
    Link,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";
import { useParams, useHistory } from "react-router";
import { gql, useLazyQuery, useMutation } from "@apollo/client";

import TextField from "../common/FormsyTextField";

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        width: "100%",
        minHeight: "100vh",
        backgroundColor: theme.palette.background.default,
    },
    toolbar: {
        backgroundColor: (theme.palette.background as any).level2,
        width: "100%",
    },
    toolbarContainer: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
        width: "100%",
    },
    link: {
        cursor: "pointer",
    },
    editor: {
        display: "flex",
        flexDirection: "column",
        padding: theme.spacing(4),
    },
    title: {},
    imageURL: {
        marginTop: theme.spacing(2),
    },
    description: {
        marginTop: theme.spacing(2),
    },
    content: {
        marginTop: theme.spacing(2),
    },
    contentTextArea: {
        resize: "vertical",
        minHeight: 400,
        width: "100%",
    },
    actions: {
        width: "100%",
        display: "flex",
        justifyContent: "flex-end",
        flexDirection: "row",
        alignItems: "center",
    },
    action: {},
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

interface Params {
    id: string;
}

const CREATE_EMPTY_ARTICLE = gql`
    mutation CreateEmptyArticle {
        createArticle {
            id
        }
    }
`;

const GET_ARTICLE = gql`
    query GetArticle($articleId: ID!) {
        getArticleById(articleId: $articleId) {
            id
            title
            imageURL
            description
            content
            status
        }
    }
`;

const UPDATE_ARTICLE = gql`
    mutation UpdateArticle(
        $articleId: ID!
        $title: String
        $description: String
        $content: String
        $imageURL: String
    ) {
        updateArticle(
            articleId: $articleId
            title: $title
            description: $description
            content: $content
            imageURL: $imageURL
        ) {
            id
        }
    }
`;

const PUBLISH_ARTICLE = gql`
    mutation PublishArticle($articleId: ID!) {
        publishArticle(articleId: $articleId) {
            status
        }
    }
`;

const UNPUBLISH_ARTICLE = gql`
    mutation UnpublishArticle($articleId: ID!) {
        unpublishArticle(articleId: $articleId) {
            status
        }
    }
`;

const DELETE_ARTICLE = gql`
    mutation DeleteArticle($articleId: ID!) {
        deleteArticle(articleId: $articleId)
    }
`;

const ArticleEditor: FunctionComponent = () => {
    const classes = useStyles();
    const params = useParams<Params>();
    const [
        getArticle,
        { loading: fetching, error: fetchError, data: currentArticleData },
    ] = useLazyQuery(GET_ARTICLE, {
        variables: { articleId: params.articleId },
    });

    const [
        createEmptyArticle,
        { loading: creating, error: newError, data: newArticle },
    ] = useMutation(CREATE_EMPTY_ARTICLE);
    const [updateArticle, { loading: updating, error: updateError }] =
        useMutation(UPDATE_ARTICLE, {
            refetchQueries: [GET_ARTICLE],
        });

    const [publishArticle, { loading: publishing, error: publishError }] =
        useMutation(PUBLISH_ARTICLE, {
            refetchQueries: [GET_ARTICLE],
        });
    const [unpublishArticle, { loading: unpublishing, error: unpublishError }] =
        useMutation(UNPUBLISH_ARTICLE, {
            refetchQueries: [GET_ARTICLE],
        });
    const history = useHistory();

    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [imageURL, setImageURL] = useState("");
    const [description, setDescription] = useState("");
    const [valid, setValid] = useState(false);
    const previousCreating = useRef<boolean>(false);

    const currentArticle = useMemo(
        () => currentArticleData?.getArticleById || {},
        [currentArticleData?.getArticleById],
    );

    const changesMade =
        currentArticle?.title !== title ||
        currentArticle?.content !== content ||
        currentArticle?.imageURL !== imageURL ||
        currentArticle?.description !== description;

    useEffect(() => {
        if (params.id === "new" && !previousCreating.current && !creating) {
            createEmptyArticle();
        }

        if (params.id !== "new" && currentArticle?.id !== params.id) {
            getArticle({ variables: { articleId: params.id } });
        }

        if (previousCreating.current && !creating) {
            history.push(`/articles/${newArticle?.createArticle?.id}/edit`);
        }

        previousCreating.current = creating;
    }, [
        createEmptyArticle,
        creating,
        currentArticle?.id,
        getArticle,
        history,
        newArticle?.createArticle?.id,
        params.id,
    ]);

    useEffect(() => {
        if (currentArticleData) {
            setTitle(currentArticle?.title);
            setImageURL(currentArticle?.imageURL);
            setDescription(currentArticle?.description);
            setContent(currentArticle?.content);
        }
    }, [
        currentArticle?.title,
        currentArticle?.imageURL,
        currentArticle?.description,
        currentArticle?.content,
        currentArticleData,
    ]);

    const handleContentChange = useCallback((event) => {
        setContent(event.target.value);
    }, []);

    const handleTitleChange = useCallback((event) => {
        setTitle(event.target.value);
    }, []);

    const handleImageURLChange = useCallback((event) => {
        setImageURL(event.target.value);
    }, []);

    const handleDescriptionChange = useCallback((event) => {
        setDescription(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleOpenArticles = useCallback(() => {
        history.push("/articles");
    }, [history]);

    const handlePublishArticle = useCallback(() => {
        (currentArticle?.status === "public"
            ? unpublishArticle
            : publishArticle)({
            variables: { articleId: currentArticle?.id },
        });
    }, [currentArticle, publishArticle, unpublishArticle]);

    const handleSave = useCallback(() => {
        updateArticle({
            variables: {
                articleId: currentArticle?.id,
                title,
                imageURL,
                description,
                content,
            },
        });
    }, [
        content,
        currentArticle?.id,
        description,
        title,
        imageURL,
        updateArticle,
    ]);

    return (
        <div className={classes.root}>
            <Toolbar className={classes.toolbar}>
                <div className={classes.toolbarContainer}>
                    <Breadcrumbs separator="â€º">
                        <Link
                            color="inherit"
                            onClick={handleOpenArticles}
                            className={classes.link}>
                            Articles
                        </Link>
                    </Breadcrumbs>
                    <div className={classes.actions}>
                        <Button
                            onClick={handlePublishArticle}
                            className={classes.action}>
                            <Icon
                                className={classes.actionIcon}
                                fontSize="small">
                                publish
                            </Icon>
                            {currentArticle?.status === "public"
                                ? "Unpublish"
                                : "Publish"}
                        </Button>
                        <Button
                            onClick={handleSave}
                            className={classes.action}
                            disabled={!changesMade || !valid || updating}>
                            {updating && (
                                <CircularProgress
                                    size="16px"
                                    color="inherit"
                                    className={classes.progress}
                                />
                            )}
                            {!updating && (
                                <Icon className={classes.actionIcon}>save</Icon>
                            )}
                            Save
                        </Button>
                    </div>
                </div>
            </Toolbar>
            <div className={classes.editor}>
                <Formsy onValid={handleValidForm} onInvalid={handleInvalidForm}>
                    <TextField
                        name="title"
                        className={classes.title}
                        label="Title"
                        validations={{
                            minLength: 8,
                            maxLength: 256,
                        }}
                        validationErrors={{
                            minLength: "The title should have 8-256 characters",
                            maxLength: "The title should have 8-256 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={title}
                        onChange={handleTitleChange}
                        required={true}
                        fullWidth={true}
                    />

                    <TextField
                        name="description"
                        className={classes.description}
                        label="Description"
                        validations={{
                            maxLength: 1024,
                        }}
                        validationErrors={{
                            maxLength:
                                "The description cannot be longer than 1024 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={description}
                        onChange={handleDescriptionChange}
                        fullWidth={true}
                        multiline={true}
                        rows={5}
                    />

                    <TextField
                        name="imageURL"
                        className={classes.imageURL}
                        label="Image URL"
                        validations={{
                            matchRegexp:
                                /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/,
                        }}
                        validationErrors={{
                            matchRegexp:
                                "The URL you have entered is not valid",
                        }}
                        variant="outlined"
                        size="medium"
                        value={imageURL}
                        onChange={handleImageURLChange}
                        fullWidth={true}
                    />

                    <TextField
                        name="content"
                        className={classes.content}
                        label="Content"
                        validations={{
                            maxLength: 20480,
                        }}
                        validationErrors={{
                            maxLength:
                                "The article content cannot be longer than 20480 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={content}
                        onChange={handleContentChange}
                        fullWidth={true}
                        multiline={true}
                        inputProps={{
                            className: classes.contentTextArea,
                        }}
                    />
                </Formsy>
            </div>
        </div>
    );
};

export default ArticleEditor;
